import gsap from "gsap";
import { showToast } from "./info/toast";
import storage from "./storage";
import products from "../../data/products.json";

//        
export function initProductQuickView(): void {
  if (typeof window === "undefined") return;

  const modal = document.getElementById("product-modal");
  if (!modal) return;
  const modalEl = modal as HTMLElement;

  const titleEl = modalEl.querySelector<HTMLElement>(".product-modal__title"); // 
  const descEl = modalEl.querySelector<HTMLElement>(".product-modal__desc"); //  
  const priceEl = modalEl.querySelector<HTMLElement>(".product-modal__price"); // 
  const thumbEl = modalEl.querySelector<HTMLElement>(".product-modal__thumb"); // 
  const closeBtn = modalEl.querySelector<HTMLElement>(".product-modal__close"); //  
  const addBtn = modalEl.querySelector<HTMLButtonElement>(
    ".product-modal__add"
  ); //   
  const favBtn = modalEl.querySelector<HTMLButtonElement>(
    ".product-modal__fav"
  ); //  

  let isOpen = false; //  
  let isAnimating = false; //   
  let lastFocusedBeforeOpen: HTMLElement | null = null; //     
  //     
  type ProductPreview = {
    id?: string;
    title?: string;
    desc?: string;
    price?: string;
    thumb?: string;
  };

  let currentData: ProductPreview | null = null; //     

  type CartItem = { id: string; qty: number }; //   

  document.addEventListener("click", (e) => {
    const el = e.target as HTMLElement;

    //  
    if (el.closest(".quick-view")) {
      console.log("");
      const card = el.closest(".product-card") as HTMLElement | null;
      if (!card) return;

      const data = {
        id: card.dataset.id,
        title: card.dataset.title,
        desc: card.dataset.desc,
        price: card.dataset.price,
        thumb: card.dataset.thumb,
      };

      e.preventDefault();
      currentData = data;
      open(data);

      const qty = getCartQty(data.id);

      qty > 0
        ? addBtn?.classList.add("is-added")
        : addBtn?.classList.remove("is-added");

      //   ID  , ID  title .
      const fav = isFavorited(data.id ?? data.title);
      if (favBtn) {
        if (fav) {
          favBtn.classList.add("is-favorited");
          favBtn.textContent = " ";
        } else {
          favBtn.classList.remove("is-favorited");
          favBtn.textContent = "";
        }
      }
      return;
    }

    if (
      el.matches("[data-dismiss=modal]") ||
      el.closest("[data-dismiss=modal]")
    ) {
      close();
    }

    if (el.closest(".product-modal__add")) {
      if (!currentData || !currentData.id) return;
      const id = currentData.id;
      const title = currentData.title ?? "";
      const items = getCartItems();
      const found = items.find((x) => x.id === id);

      found
        ? (found.qty = Math.max(1, found.qty + 1))
        : items.push({ id, qty: 1 });

      setCartItems(items);

      document.dispatchEvent(
        new CustomEvent("cart:changed", { detail: { id } }) //   
      );

      showToast(`${title}()  `);
      if (addBtn) addBtn.textContent = ` +1`;
      close();
      return;
    }

    if (el.closest(".product-modal__fav")) {
      if (!currentData || !currentData.id) return;
      const id = currentData.id;
      const title = currentData.title ?? "";
      const favs = getFavorites();
      if (favs.includes(id)) {
        const remaining = favs.filter((x) => x !== id);
        setFavorites(remaining);
        document.dispatchEvent(
          new CustomEvent("favorites:changed", { detail: { id } })
        );
        showToast(`${title}()  `);
        favBtn?.classList.remove("is-favorited");
        if (favBtn) favBtn.textContent = "";
      } else {
        favs.push(id);
        setFavorites(favs);
        document.dispatchEvent(
          new CustomEvent("favorites:changed", { detail: { id } })
        );
        showToast(`${title}()  `);
        favBtn?.classList.add("is-favorited");
        if (favBtn) favBtn.textContent = " ";
      }
      return;
    }
  });

  function open(data: {
    id?: string;
    title?: string;
    desc?: string;
    price?: string;
    thumb?: string;
  }) {
    if (isOpen || isAnimating) return;
    isAnimating = true;

    lastFocusedBeforeOpen = document.activeElement as HTMLElement | null; //    
    modalEl.classList.remove("d-none");
    modalEl.setAttribute("aria-hidden", "false");

    if (titleEl) titleEl.textContent = data.title ?? "";
    if (descEl) descEl.textContent = data.desc ?? " ";
    if (priceEl) priceEl.textContent = data.price ?? "0";
    if (thumbEl) thumbEl.textContent = data.thumb ?? "";

    const panel = modalEl.querySelector<HTMLElement>(".product-modal__panel");
    if (panel) {
      gsap.set(panel, { autoAlpha: 0, scale: 0.96, y: 10 });
      gsap.to(panel, {
        autoAlpha: 1,
        scale: 1,
        y: 0,
        duration: 0.32,
        ease: "power2.out",
        onComplete: () => {
          isAnimating = false;
          isOpen = true;
          (closeBtn as HTMLElement | null)?.focus(); //      
        },
      });
    } else {
      console.warn("@@@@@@@@@@@@@@   @@@@@@@@@@@@@");

      return;
      isAnimating = false;
      isOpen = true;
      (closeBtn as HTMLElement | null)?.focus();
    }
  }

  function isFavorited(id?: string) {
    if (!id) return false;
    const favs = getFavorites();
    if (favs.includes(id)) return true;
    // id  title   : title    id favorites  
    const p = (products as any[]).find((x) => x.title === id);
    return !!(p && favs.includes(p.id));
  }

  // get & set  
  function getFavorites(): string[] {
    const raw = storage.getItemPrefer("favorites");
    if (!raw) return [];
    const parsed = JSON.parse(raw);

    //   :  id  title  
    if (
      Array.isArray(parsed) &&
      parsed.length &&
      typeof parsed[0] === "string"
    ) {
      // legacy:  id  title  id  
      const mapped = (parsed as string[])
        .map((s) => {
          const p = (products as any[]).find(
            (x) => x.id === s || x.title === s
          );
          return p?.id ?? null;
        })
        .filter(Boolean) as string[];
      setFavorites(mapped);
      return mapped;
    }
    return parsed as string[];
  }

  function setFavorites(items: string[]) {
    storage.setItemPrefer("favorites", JSON.stringify(items));
  }

  function close() {
    if (!isOpen || isAnimating) return;
    isAnimating = true;
    const panel = modalEl.querySelector<HTMLElement>(".product-modal__panel");

    if (panel) {
      gsap.to(panel, {
        autoAlpha: 0,
        scale: 0.96,
        y: 10,
        duration: 0.24,
        ease: "power2.in",
        onComplete: finishClose,
      });
    } else {
      finishClose();
    }
  }

  function finishClose() {
    const active = document.activeElement as HTMLElement | null;
    if (active && modalEl.contains(active)) {
      active.blur();
    }
    modalEl.classList.add("d-none");
    modalEl.setAttribute("aria-hidden", "true");

    if (lastFocusedBeforeOpen && !modalEl.contains(lastFocusedBeforeOpen)) {
      lastFocusedBeforeOpen.focus();
    }

    lastFocusedBeforeOpen = null;
    isAnimating = false;
    isOpen = false;
  }

  // get & set  
  function getCartItems(): CartItem[] {
    const raw = storage.getItemPrefer("cartItems");
    if (!raw) return [];
    const parsed = JSON.parse(raw);
    if (
      Array.isArray(parsed) &&
      parsed.length &&
      typeof parsed[0] === "string"
    ) {
      // legacy:  id  title    
      const converted: CartItem[] = (parsed as string[])
        .map((s) => {
          const p = (products as any[]).find(
            (x) => x.id === s || x.title === s
          );
          return p && p.id ? { id: p.id, qty: 1 } : { id: s, qty: 1 };
        })
        .filter(Boolean);
      setCartItems(converted);
      return converted;
    }
    return parsed as CartItem[];
  }

  function setCartItems(items: CartItem[]) {
    storage.setItemPrefer("cartItems", JSON.stringify(items));
  }

  function getCartQty(id?: string) {
    if (!id) return 0;
    const items = getCartItems();
    const found = items.find((x) => x.id === id);
    return found ? found.qty : 0;
  }

  closeBtn?.addEventListener("click", close);

  document.addEventListener("keydown", (ev) => {
    if (ev.key === "Escape") close();
  });
}

export default initProductQuickView;
