export const LOGIN_STORAGE_KEY = "shoppingts-info-authed"; //    
export const LOGIN_USER_KEY = "shoppingts-info-username"; //     
export const USERS_STORAGE_KEY = "shoppingts-info-users"; //    

export type AddressEntry = {
  id: string;
  tag: string;
  label: string;
  road: string;
  detail?: string;
  isDefault?: boolean;
};

export type StoredUser = {
  username: string;
  name: string;
  email: string;
  password: string;
  phone: string;
  address: string;
  profileImage?: string;
  roadAddress?: string;
  addressDetail?: string;
  addresses?: AddressEntry[];
};

type ValidationResult = { ok: boolean; message?: string };

export function saveUsers(users: StoredUser[]) {
  localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(users));
}

export function isEmailTaken(email: string) {
  const target = email.toLowerCase();
  return loadUsers().some((user) => user.email.toLowerCase() === target);
}

export function validateName(value: string): ValidationResult {
  if (!value) return { ok: false, message: " ." };
  if (value.length < 2)
    return { ok: false, message: " 2  ." };
  if (!/^[\p{L}\s]+$/u.test(value)) {
    return {
      ok: false,
      message: "     .",
    };
  }
  return { ok: true };
}

export function validateEmail(value: string): ValidationResult {
  if (!value) return { ok: false, message: " ." };
  const emailRegex = /^[\w.+-]+@[\w.-]+\.[a-zA-Z]{2,}$/;
  if (!emailRegex.test(value))
    return { ok: false, message: "   ." };
  return { ok: true };
}

export function validatePhone(value: string): ValidationResult {
  if (!value) return { ok: false, message: "  ." };
  const digits = value.replace(/\D/g, "");
  if (digits.length < 10 || digits.length > 11)
    return { ok: false, message: "  10~11 ." };
  if (!/^01[016789]\d{7,8}$/.test(digits))
    return { ok: false, message: "    ." };
  return { ok: true };
}

export function validatePassword(value: string): ValidationResult {
  if (value.length < 6)
    return { ok: false, message: " 6  ." };
  if (!/[A-Z]/.test(value))
    return { ok: false, message: "  1 ." };
  if (!/[a-z]/.test(value))
    return { ok: false, message: "  1 ." };
  if (!/[^A-Za-z0-9]/.test(value))
    return { ok: false, message: "  1 ." };
  return { ok: true };
}

export function setHintState(
  hint: HTMLElement | undefined,
  message: string,
  state: "default" | "success" | "error"
) {
  if (!hint) return;
  hint.textContent = message;
  hint.classList.remove("is-success", "is-error");
  if (state === "success") hint.classList.add("is-success");
  if (state === "error") hint.classList.add("is-error");
}

//    
export function runDuplicateCheck({
  input,
  hint,
  validator,
  isTaken,
  duplicateMessage,
  successMessage,
}: {
  input: HTMLInputElement;
  hint?: HTMLElement;
  validator: (value: string) => ValidationResult;
  isTaken: (value: string) => boolean;
  duplicateMessage: string;
  successMessage: string;
}) {
  const value = input.value.trim();
  const validation = validator(value);
  if (!validation.ok) {
    setHintState(hint, validation.message ?? " .", "error");
    input.dataset.duplicate = "false";
    input.focus();
    return;
  }
  const exists = isTaken(value);
  if (exists) {
    setHintState(hint, duplicateMessage, "error");
    input.dataset.duplicate = "false";
    return;
  }
  setHintState(hint, successMessage, "success");
  input.dataset.duplicate = "true";
}

export function validateUsername(value: string): ValidationResult {
  if (!value) return { ok: false, message: " ." };
  if (value.length < 4)
    return { ok: false, message: " 4  ." };
  if (!/^[a-zA-Z0-9]+$/.test(value))
    return { ok: false, message: "    ." };
  if (!/[a-zA-Z]/.test(value) || !/\d/.test(value)) {
    return { ok: false, message: "    ." };
  }
  return { ok: true };
}

export function isUsernameTaken(username: string) {
  return findUserByUsername(username) ? true : false;
}

export function findUserByUsername(username: string): StoredUser | undefined {
  const target = username.toLowerCase();
  return loadUsers().find((user) => user.username.toLowerCase() === target);
}

export function findUserByEmail(email: string): StoredUser | undefined {
  if (!email) return undefined;
  const target = email.toLowerCase();
  return loadUsers().find((user) => user.email.toLowerCase() === target);
}

//   
export function loadUsers(): StoredUser[] {
  const raw = localStorage.getItem(USERS_STORAGE_KEY);
  if (!raw) return [];
  const parsed = JSON.parse(raw);
  if (!Array.isArray(parsed)) return [];
  const normalized: StoredUser[] = [];

  parsed.forEach((user) => {
    if (
      typeof user?.username === "string" &&
      typeof user?.email === "string" &&
      typeof user?.password === "string"
    ) {
      const resolvedName =
        typeof user.name === "string" && user.name.trim()
          ? (user.name as string)
          : (user.username as string);

      const legacyRoad =
        typeof user.roadAddress === "string" ? user.roadAddress : undefined;
      const legacyDetail =
        typeof user.addressDetail === "string" ? user.addressDetail : undefined;
      const legacyFull = typeof user.address === "string" ? user.address : "";

      let addresses: AddressEntry[] | undefined;
      if (Array.isArray(user.addresses)) {
        addresses = user.addresses
          .map((a: any): AddressEntry | null => {
            if (!a || typeof a.road !== "string" || !a.road.trim()) return null;
            return {
              id: typeof a.id === "string" && a.id ? a.id : cryptoRandomId(),
              tag: typeof a.tag === "string" && a.tag ? a.tag : "home",
              label:
                typeof a.label === "string" && a.label.trim()
                  ? a.label.trim()
                  : " ",
              road: a.road.trim(),
              detail:
                typeof a.detail === "string" && a.detail.trim()
                  ? a.detail.trim()
                  : undefined,
              isDefault: a.isDefault === true,
            };
          })
          .filter((a: AddressEntry | null): a is AddressEntry => Boolean(a));
      }

      if (!addresses || !addresses.length) {
        if (legacyRoad || legacyFull) {
          addresses = [
            {
              id: cryptoRandomId(),
              tag: "home",
              label: " ",
              road: (legacyRoad || legacyFull || "").trim(),
              detail: legacyDetail?.trim() || undefined,
              isDefault: true,
            },
          ];
        }
      }

      // ensure only one default
      if (addresses && addresses.length) {
        const hasDefault = addresses.some((a) => a.isDefault);
        if (!hasDefault) addresses[0].isDefault = true;
      }

      normalized.push({
        username: user.username,
        name: resolvedName,
        email: user.email,
        password: user.password,
        phone: typeof user.phone === "string" ? user.phone : "",
        profileImage:
          typeof user.profileImage === "string" ? user.profileImage : undefined,
        address: legacyFull,
        roadAddress: legacyRoad,
        addressDetail: legacyDetail,
        addresses,
      });
    }
  });
  return normalized; //   
}

export function updateUser(username: string, patch: Partial<StoredUser>) {
  const users = loadUsers();
  const idx = users.findIndex(
    (u) => u.username.toLowerCase() === username.toLowerCase()
  );
  if (idx === -1) return false;
  users[idx] = { ...users[idx], ...patch };
  saveUsers(users);
  return true;
}

function cryptoRandomId() {
  try {
    if (
      typeof crypto !== "undefined" &&
      typeof crypto.randomUUID === "function"
    ) {
      return crypto.randomUUID();
    }
  } catch {}
  return `addr-${Math.random().toString(36).slice(2, 10)}`;
}
